
.applyForJobFormWrapper .inputTextWrapper{
    width: 50%;
    float: left;
    padding: 10px 20px;
    box-sizing: border-box;
}
.applyForJobFormWrapper .inputField{
    position: relative;
    width: 100%;
}
.applyForJobFormWrapper .inputField .MuiFormHelperText-root{
    position: absolute;
    bottom: 3px;
    right: -5px;
    font-style: italic;
}
.applyForJobFormWrapper .submitJobBtnWrapper{
    width: 100%;
    float: left;
    justify-content: center;
    display: flex;
    margin-top: 25px;
}
.applyForJobFormWrapper .submitJobBtnWrapper .navBackBtn{
    margin-right: 25px;
}
@media screen and (max-width: 992px) {
    .applyForJobFormWrapper .inputTextWrapper{
        width: 100%;
    }
}