.job-ads-container * {
    transition: all .2s ease;
}
.job-ads-container{
    font-family: inherit;
}
.job-ads-container .jobAdsTable{
    min-width: 100px;
}
.floatLeft{
    float: left;
}
.floatRight{
    float: right;
}
.w100{
    width: 100%;
}
.textCenter{
    text-align: center;
}
.job-ads-container button:hover{
    opacity:0.95;
    transition: all .3s ease;
}
.job-ads-container button:disabled{
    background-color: #ccc !important;
    cursor: not-allowed !important;
    color: rgba(0, 0, 0, 0.26) !important;
}