.jobAdsTableComponentWrapper .jobListRow{
    cursor: pointer;
}
.jobAdsTableComponentWrapper  .tableHeaderRow{
    border-bottom: 2px solid #333;
}
.jobAdsTableComponentWrapper .jobListRow:hover{
    background-color: rgba(0, 0, 0, 0.04);
}
.jobAdsTableComponentWrapper .jobListRow:hover > td{
